/*
To customize the look and feel of Ionic, you can override the variables
in ionic's _variables.scss file.

For example, you might change some of the default colors:

$light:                           #fff !default;
$stable:                          #f8f8f8 !default;
$positive:                        #387ef5 !default;
$calm:                            #11c1f3 !default;
$balanced:                        #33cd5f !default;
$energized:                       #ffc900 !default;
$assertive:                       #ef473a !default;
$royal:                           #886aea !default;
$dark:                            #444 !default;
*/

// 下面用了占位符作为路径，在构建时动态获取 jspm_packages 里 ionic 所在文件夹的位置然后替换
// The path for our ionicons font files, relative to the built CSS in www/bundles
$ionicons-font-path: "../jspm_packages/github/driftyco/ionic-bower@1.0.0/fonts" !default;
@import "www/jspm_packages/github/driftyco/ionic-bower@1.0.0/scss/ionic.scss";  // 引入 ionic 的所有 scss 文件

@import "./helper.scss"; // 引入一些方便开发的通用样式

// 修复图标在 header bar 上过大的问题
.button-bar-icon {
  .icon:before,
  &.icon:before {
    font-size: $button-bar-button-icon-size !important;
  }
}

// 修复中英文断行
.item-text-wrap .item,
.item-text-wrap .item-content,
.item-text-wrap,
.item-text-wrap h1,
.item-text-wrap h2,
.item-text-wrap h3,
.item-text-wrap h4,
.item-text-wrap h5,
.item-text-wrap h6,
.item-text-wrap p {
  word-break: break-all;  // 强制英文单词断行
  hyphens: auto;          // 英文单词断行用连字符，不过除了火狐外的浏览器都还不支持
}

// 帖子列表
.topic-item {
  .topic-item-meta {
    // 因为里面包含的几个 small 没有占满高度，导致下方空隙看起来大一点，所以要 margin 负值补偿
    margin-bottom: -4px !important;
  }

  small:not(:last-of-type), .icon {
    margin-right: 0.25em;
  }

  .topic-item-boardname {
    background-color: #f5f5f5;
    color: #999;
    padding: 2px 4px;
    border-radius: 2px;
  }
}

// 下拉刷新的默认字体大小是 16px 太大了（帖子标题用的 h3 才 14px），这里改小一点
.scroll-refresher {
  .ionic-refresher-content {
    bottom: 16px !important;
    .text-refreshing,
    .text-pulling {
      font-size: 14px !important;
      line-height: 14px !important;
    }
  }
}

// 帖子详情
.topic-detail {
  .posts-from {
    @extend .item;
    @extend .balanced;

    padding-top: 6px;
    padding-bottom: 6px;
    min-height: 24px;
    border: none;

    font-size: 12px;
    font-weight: 100;
    background-color: #f8f8f8;
  }

  .post-item:nth-child(2) {
    border-top: none;
  }
}
